import Image from "react-bootstrap/Image";
import Container from "react-bootstrap/Container";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import { useEffect } from "react";
import {
  NavLink,
  Outlet,
  useLocation,
  useSearchParams,
} from "react-router-dom";
import { connect } from "react-redux";
import { createItemAction } from "../../redux/actions/item";
import { BASE_URI } from "../../redux/constant";

function ProductDetail({ itemData, fetchItem }) {
  const [search, setSearch] = useSearchParams();
  const id = search.get("id");

  useEffect(() => {
    if (id !== null) {
      fetchItem(id);
    }
  }, []);
  return (
    <>
      <Container className="my-3">
        <Row style={{ color: "#777777", fontSize: "14px" }}>
          <Col md={3}>
            <div>{itemData.name}</div>
            <div></div>
          </Col>
          <Col md={3}>
            <div></div>
            <div> /Q</div>
          </Col>
          <Col md={3}>
            <div>{itemData.created}</div>
          </Col>
          <Col md={3}>
            <div>230g</div>
          </Col>
        </Row>
      </Container>
      <Image
        className="mt-3 mb-5"
        // src={require("../../img/" +itemData.idesc)}
        src={`${BASE_URI}/img/${itemData.idesc}`}
        style={{ width: "63.5%", marginLeft: "20%" }}
      />
    </>
  );
}

function mapStateToProps(state) {
  return {
    itemData: state.item,
  };
}

function mapDispatchToProps(dispatch) {
  return {
    fetchItem: (id) => {
      //redux
      dispatch(createItemAction(id));
    },
  };
}

//connect()()Count
export default connect(mapStateToProps, mapDispatchToProps)(ProductDetail);
